@import url('https://fonts.googleapis.com/css2?family=Bricolage+Grotesque&family=Jua&family=Noto+Sans+KR:wght@700&family=Noto+Serif+KR:wght@600&display=swap');
@import '../scss/_variables.scss';
@import "/node_modules/animate-sass/animate";
@import 'animate.css';
$mobile : "(max-width: 575px)";

.content_h{
    flex: 1;
}

.swiper-container {
    width: 100%;

    .slide-1{
      background: url("../images/inkwang_img/inkwang_front.jpeg");
      background-size: 100% 100%;
      filter: brightness(75%);
      height: 100%;
    }

    .slide-2{
      background: url("../images/inkwang_img/inkwang_img01.jpeg");
      background-size: 100% 100%;
      height: 100%;
      filter: brightness(75%);
    }

    .slide-3{
      background: url("../images/inkwang_img/inkwang_8.jpeg");
      background-size: 100% 100%;
      height: 100%;
      filter: brightness(75%);
    }

    .slide-4{
      background: url("../images/inkwang_img/inkwang_9.jpeg");
      background-size: 100% 100%;
      height: 100%;
      filter: brightness(75%);
    }

    .slider-sub-title {
      font-family: $logo-font;
      font-size: 55px;
      letter-spacing: 3px;
      font-style: normal;
      font-stretch: normal;
      line-height: 65px;
      color: #ffffff;
      position: absolute;
      top: 62%;
      left: 10%;
      @media ($mobile) {
        font-size: 25px;
        line-height: 35px;
        top: 70%;
      }
    }

    p {
      font-family: 'Noto Sans KR', sans-serif;
      text-shadow: 5px 5px 2px #0000005b;
      line-height: 1.27;
    }

    .t_4{
      height: 400px;
      line-height: 75px;
      background-color: #0000008a;
      padding-top: 80px;
      top: 30%;
      bottom: 25%;
      left: 25%;
      right: 25%;
      text-align: center;
      width: 800px;
      @media ($mobile) {
        width: 75%;
        left: 15%;
        top: 30%;
        height: 40%;
        line-height: 50px;
      }
    }

    .swiper-pagination-bullet {
      background: rgb(255, 255, 255) !important;
      opacity: 0.7;
    }
    .swiper-pagination-bullet-active {
      background: #372aca !important;
      opacity: 1;
    }
}
    


